<template>
  <div class="catalog">
    <div class="catatop">
      <p>
        <span>{{ timer }}</span>
      </p>
      <div class="sort">
        <p><van-icon name="notes-o" /></p>
        <p><van-icon name="apps-o" /></p>
      </div>
    </div>
    <div class="list">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text=""
        @load="onLoad"
      >
        <ul>
          <lazy-component>
            <li
              v-for="(item, index) in comic.chapter_list"
              :key="index"
              @click="Jumping(comic.chapter_list, index)"
            >
              <div class="imgs">
                <img
                  v-lazy="
                    'https://mhpic.xiaomingtaiji.net/comic/' +
                    item.chapter_image.high.replace('$$', '1')
                  "
                />
              </div>
              <div class="leftFont">
                <p>{{ item.chapter_name }}<b></b>{{ item.chapter_title }}</p>
                <span
                  >{{ listTime[index]
                  }}<span
                    class="now"
                    v-if="item.create_time >= Date.now() - ADay"
                    >new</span
                  ></span
                >
              </div>
            </li>
          </lazy-component>
        </ul>
      </van-list>
    </div>
  </div>
</template>

<script>
import news from "../assets/new.png";
export default {
  props: ["comic", "arrow"],
  data() {
    return {
      list: [],
      loading: false,
      finished: false,
      news: news,
      ADay: 24 * 60 * 60 * 1000 * 15,
    };
  },
  methods: {
    onLoad() {
      // 
      // setTimeout  ajax 
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          this.list.push(this.list.length + 1);
        }

        // 
        this.loading = false;

        // 
        if (this.list.length >= 40) {
          this.finished = true;
        }
      }, 1000);
    },
    //  
    timeFont(n) {
      var timeOld = new Date(parseInt(n))
        .toLocaleDateString()
        .replace("/", ".")
        .replace("/", ".");
      var timeNew = timeOld.split(".");
      if (timeNew[1].length == 1) {
        timeNew[1] = "0" + timeNew[1];
      }
      if (timeNew[2].length == 1) {
        timeNew[2] = "0" + timeNew[2];
      }
      return timeNew.join(".");
    },
    Jumping(n, m) {
      this.$store.commit("falseFooterFlag");
      // this.$router.push({
      //   path: "/Play",
      //   query: { own: n, index: m },
      // });
      this.$router.push({
        name: "Play",
        params: { own: n, index: m },
      });
    },
  },
  computed: {
    timer() {
      if (typeof this.comic.update_time == "string") {
        return this.timeFont(this.comic.update_time);
      } else {
        return null;
      }
    },
    listTime() {
      if (typeof this.comic.chapter_list == "object") {
        var listTimeVar = [];
        for (var i = 0; i < this.comic.chapter_list.length; i++) {
          listTimeVar.push(
            this.timeFont(this.comic.chapter_list[i].create_time)
          );
        }
        return listTimeVar;
      } else {
        return [];
      }
    },
  },
};
</script>

<style lang='less'>
.catalog {
  .catatop {
    display: flex;
    border-bottom: 1px solid #f5f5f5;
    padding-bottom: 10px;
    & > p {
      text-align: left;
      flex: 1 1 auto;
      color: #999;
      span {
        color: black;
      }
    }
    .sort {
      display: flex;
      p {
        margin-left: 15px;
        display: flex;
        align-items: center;
      }
    }
  }
  .list {
    ul {
      padding: 15px 0px;
      .van-pull-refresh {
        overflow: visible !important;
      }
      li {
        display: flex;
        padding-bottom: 15px;
        .imgs {
          min-width: 120px;
          height: 60px;
          border-radius: 5px;
          overflow: hidden;
          position: relative;
          left: 0;
          border: 1px solid black;
          img {
            width: 120px;
            position: absolute;
            left: 0;
            top: -60px;
          }
        }
        .leftFont {
          flex: 1 1 auto;
          text-align: left;
          padding-left: 10px;
          display: flex;
          flex-wrap: wrap;
          p {
            width: 100%;
            b {
              width: 15px;
              display: inline-block;
            }
          }
          span {
            align-self: flex-end;
            font-size: 12px;
            display: flex;
            align-items: flex-end;
            position: relative;
            .now {
              display: inline-block;
              margin-left: 10px;
              background-color: #fc6454;
              padding: 1px 5px;
              font-size: 10px;
              color: white;
              border-radius: 8px;
              line-height: 15px;
            }
          }
        }
      }
    }
  }
}
</style>