<template>
  <div class="ComicsList">
    <div class="top">
      <div class="top_nav">
        <van-nav-bar>
          <template #left>
            <span @click="returnNow">(  )</span>
          </template>
          <template #right>
            <van-icon name="like-o" />
            <van-icon name="chat-o" dot />
          </template>
        </van-nav-bar>
      </div>
      <div
        class="background"
        :style="{
          background:
            'url(https://image.zymkcdn.com/file/cover/' +
            cmoicid(comic.comic_id) +
            '_3_4.jpg-300x400.jpg.webp)',
        }"
      ></div>
      <div class="cover">
        <div class="left">
          <img
            :src="
              'https://image.zymkcdn.com/file/cover/' +
              cmoicid(comic.comic_id) +
              '_3_4.jpg-300x400.jpg.webp'
            "
          />
          <div class="pingfen">
            <van-icon name="star" /><span>{{ comic.score / 10 }}</span>
          </div>
        </div>
        <div class="right">
          <h1>{{ comic.comic_name }}</h1>
          <p>{{ comic.author_name }}</p>
          <ul>
            <li v-for="(item,index) in comic.comic_type" :key="item.id" :style="{backgroundColor:nowColor[index]}">
              {{ item.name }}
            </li>
          </ul>
          <div class="hot">
            <van-icon name="fire-o" /><span>{{ totalClick }}</span>
          </div>
          <div class="read">
            <span></span>
            <span v-if="readHistory"></span>
            <span v-else></span>
          </div>
        </div>
      </div>
    </div>
    <div class="nav">
      <!-- <van-sticky :offset-top="50"> -->
      <van-tabs
        v-model="active"
        :title-active-color="'#fc6454'"
        line-width="25px"
        animated
      >
        <van-tab :title="''">
          <Details :comic="comic"></Details>
        </van-tab>
        <van-tab>
          <template #title>
            <div @click="toggle" class="icon">
              
              <van-icon name="arrow-up" v-show="arrow" class="icontop" />
              <van-icon name="arrow-down" v-show="!arrow" class="iconbottom" />
            </div>
          </template>
          <Catalog :comic="comic" :arrow="arrow"></Catalog>
        </van-tab>
        <van-tab :title="''">
          <Support :comic="comic"></Support>
        </van-tab>
      </van-tabs>
      <!-- </van-sticky> -->
    </div>
  </div>
</template>

<script>
import Catalog from "@/components/Catalog.vue";
import Details from "@/components/Details.vue";
import Support from "./Support.vue";

export default {
  components: {
    Catalog,
    Details,
    Support,
  },
  data() {
    return {
      readHistory: true,
      readHistoryNumber: 1,
      id: null,
      comic: { author_info: { Uname: "" }, chapter_list: [] },
      color: ["#7bc730", "#ff7270", "#ffa742", "#5dbdff", "#a1a8ff", "#00cebb"],
      spanlight: true,
      active: 1,
      nowColor: [],
    };
  },
  created() {
    this.nowColor = this.shuffle(this.color);
    this.id = this.$route.query.id;
    this.$store.commit("falseFooterFlag");
    if (this.id.comic_idOld != undefined) {
      this.axios
        .get(
          "http://getcomicinfo-globalapi.zymk.cn/app_api/v5/getcomicinfo/?comic_id=" +
            this.id.comic_idOld
        )
        .then((datas) => {
          this.comic = datas.data.data;
          this.$store.commit("changeComic", this.comic);
          this.$store.commit("changeComicAbout", this.comic);
        });
    }
  },
  beforeRouteUpdate(to, from, next) {
    this.nowColor = this.shuffle(this.color);
    this.id = to.query.id;
    this.$store.commit("falseFooterFlag");
    if (this.id.comic_idOld != undefined) {
      this.axios
        .get(
          "http://getcomicinfo-globalapi.zymk.cn/app_api/v5/getcomicinfo/?comic_id=" +
            this.id.comic_idOld
        )
        .then((datas) => {
          this.comic = datas.data.data;
          this.$store.commit("changeComic", this.comic);
          this.$store.commit("changeComicAbout", this.comic);
        });
    }
    next();
  },
  activated() {
    this.nowColor = this.shuffle(this.color);
    this.id = this.$route.query.id;
    this.$store.commit("falseFooterFlag");
    console.log(this.$route.params);
    if (this.id.comic_idOld != undefined) {
      console.log(2);
      this.axios
        .get(
          "http://getcomicinfo-globalapi.zymk.cn/app_api/v5/getcomicinfo/?comic_id=" +
            this.id.comic_idOld
        )
        .then((datas) => {
          console.log(this);
          this.comic = datas.data.data;
          this.$store.commit("changeComic", this.comic);
          this.$store.commit("changeComicAbout", this.comic);
        });
    }
  },
  watch: {
    id() {
      this.nowColor = this.shuffle(this.color);
      this.id = this.$route.query.id;
      this.$store.commit("falseFooterFlag");
      if (this.id.comic_idOld != undefined) {
        this.axios
          .get(
            "http://getcomicinfo-globalapi.zymk.cn/app_api/v5/getcomicinfo/?comic_id=" +
              this.id.comic_idOld
          )
          .then((datas) => {
            this.comic = datas.data.data;
            this.$store.commit("changeComic", this.comic);
            this.$store.commit("changeComicAbout", this.comic);
          });
      }
    },
    arrow() {
      this.comic.chapter_list.reverse();
    },
  },
  methods: {
    returnNow() {
      this.$store.commit("trueFooterFlag");
      this.$router.push({ path: "/" });
    },
    toggle() {
      // (this.arrow = !this.arrow)
      this.active == 1
        ? this.$store.commit("toggleList")
        : this.active == this.active;
    },
    cmoicid(id) {
      if (typeof id == "string") {
        if (id.length == 1) {
          return "000/000/00" + id;
        } else if (id.length == 2) {
          return "000/000/0" + id;
        } else if (id.length == 3) {
          return "000/000/" + id;
        } else if (id.length == 4) {
          return "000/00" + id.slice(0, 1) + "/" + id.slice(1);
        }
      } else {
        return "000/004/557";
      }
    },
    shuffle(arr) {
      for (let i = arr.length - 1; i >= 0; i--) {
        let rIndex = Math.floor(Math.random() * (i + 1));
        // 
        // console.log(i, rIndex);
        let temp = arr[rIndex];
        arr[rIndex] = arr[i];
        arr[i] = temp;
      }
      return arr;
    },
  },
  computed: {
    totalClick() {
      if (typeof this.comic.total_click == "string") {
        // 
        if (this.comic.total_click.length > 8) {
          if (
            (this.comic.total_click / 100000000).toFixed(1).slice(-1) != "0"
          ) {
            return (this.comic.total_click / 100000000).toFixed(1) + "";
          } else {
            return Math.floor(this.comic.total_click / 100000000) + "";
          }
          // 
        } else {
          if ((this.comic.total_click / 10000).toFixed(1).slice(-1) != "0") {
            return (this.comic.total_click / 10000).toFixed(1) + "";
          } else {
            return Math.floor(this.comic.total_click / 10000) + "";
          }
        }
      } else {
        return null;
      }
    },
    arrow() {
      return this.$store.state.arrow;
    },
  },
};
</script>

<style lang='less'>
.van-tab__text--ellipsis {
  overflow: visible !important;
}
.van-pull-refresh {
  overflow: visible !important;
}
.van-sticky {
  width: 100% !important;
}
.ComicsList {
  .top {
    position: relative;
    height: 150px;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 150px;
      background: rgba(0, 0, 0, 0.3);
      position: absolute;
      top: 0;
      left: 0;
    }
    .top_nav {
      .van-hairline--bottom {
        color: white;
        background: transparent;
        &::after {
          border-bottom: none;
        }
      }
      .van-nav-bar__right {
        i {
          color: white;
          font-size: 22px;
          padding-left: 10px;
        }
      }
    }
    .background {
      position: absolute;
      top: -30px;
      left: 0;
      width: 100%;
      height: 180px;
      background-size: cover !important;
    }
    .cover {
      width: 100%;
      position: absolute;
      z-index: 333;
      top: 50px;
      left: 0;
      &::after {
        content: "";
        display: block;
        width: 0;
        height: 0;
        opacity: 0;
        clear: both;
      }
      .left {
        width: 30%;
        position: absolute;
        left: 10px;
        padding: 2px;
        background-color: white;
        max-width: 120px;
        img {
          width: 100%;
        }
        .pingfen {
          color: white;
          position: absolute;
          top: 2px;
          right: 2px;
          font-size: 10px;
          padding: 2px 3px 2px 6px;
          background: rgba(0, 0, 0, 0.5);
          border-top-left-radius: 10px;
          border-bottom-left-radius: 10px;
          display: flex;
          align-items: center;
          span {
            padding-left: 2px;
            font-size: 11px;
          }
        }
      }
      .right {
        width: 240px;
        position: absolute;
        left: 35%;
        color: white;
        font-size: 12px;
        .read {
          display: flex;
          justify-content: space-around;
          span {
            margin-top: 10px;
            display: inline-block;
            font-size: 14px;
            color: tomato;
            &:first-child {
              padding: 6px 21px;
              color: #fc6454;
              border: 1px solid #fc6454;
              border-radius: 20px;
            }
            &:last-child {
              padding: 6px 21px;
              color: white;
              border: 1px solid white;
              border-radius: 20px;
              background: #fc6454;
            }
          }
        }
        h1 {
          font-size: 20px;
          margin-bottom: 3px;
        }
        ul {
          height: 35px;
          display: flex;
          align-items: center;
          flex-wrap: wrap;
          overflow: hidden;
          li {
            margin-top: 7.5px;
            margin-bottom: 7.5px;
            margin-right: 3px;
            padding: 2px 6px;
            background-color: yellowgreen;
            white-space: nowrap;
            border-radius: 5px;
          }
        }
      }
    }
  }
  .nav {
    margin-top: 50px;
    text-align: center;
    padding: 10px 15px 0px;
    display: flex;
    justify-content: space-around;
    .van-tabs {
      width: 100%;
      .van-icon {
        padding-left: 3px;
      }
      .icon {
        position: relative;
      }
      .icontop {
        position: absolute;
        right: -16px;
        top: 0;
        z-index: 9999;
      }
      .iconbottom {
        position: absolute;
        right: -16px;
        bottom: 1px;
        z-index: 9999;
      }
    }
  }
}
</style>